@import 'mixins';

.song
{
  aspect-ratio: 2 / 1;
  max-width: 36rem;
  @media (min-aspect-ratio: 2/1){width: 90vw;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){width: 90vw;}
  @media (min-aspect-ratio: 1/2) and (max-aspect-ratio: 1/1)
  {
    transform: rotate(90deg);
    max-width: calc(90vh);
    min-width: calc(90vh);
  }
  @media (max-aspect-ratio: 1/2)
  {
    transform: rotate(90deg);
    width: 90vh;
  }
  // 0.3rem -> 5px
  position: absolute;
  background-color: whitesmoke;
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  > *
  {
    width: 100%;
  }
  .beats-list-container
  {
    height: 80%;
    overflow-x: scroll;
    overflow-y: hidden;
    .beats-list
    {
      height: 100%;
      @include flex();
      .beat
      {
        height: 100%;
        @include flex(column);
        > *
        {
          border: 1px solid black;
          width: 100%;
          @include flex();
        }
        .key
        {
          @include setColor(whitesmoke, #333);
          height: 80%;
          font-size: 2rem;
          form
          {
            width: 80%;
            @include flex(column);
            gap: 0.6rem;
            input
            {
              width: 100%;
              padding: 0.3rem 0.6rem;
              font-size: 2rem;
            }
            .submit {@include generic-text-button();}
          }
        }
        .index
        {
          @include setColor(whitesmoke, #333);
          height: 20%;
        }
      }
      .beat:hover
      {
        cursor: pointer;
      }
      .beat.selected > .key
      {
        background-color: orange;
        color: #333;
      }
    }
  }
  .beats-list-container::-webkit-scrollbar {height: 10px;}
  .beats-list-container::-webkit-scrollbar-track {background: whitesmoke;}
  .beats-list-container::-webkit-scrollbar-thumb {background: #888;}
  .beats-list-container::-webkit-scrollbar-thumb:hover {background: #555;}

  .controls
  {
    height: calc(20% - var(--gap));
    @include flex(row, space-between);
    > * { height: 100%; }
    .left
    {
      @include flex(row, flex-start);
      gap: 0.6rem;
      button.left, button.right { @include generic-icon-button();}
      button.edit, button.delete { @include generic-text-button(); }
    }
    .right
    {
      button{@include generic-text-button();}
    }
  }
}