@mixin flex($direction: row, $main: center, $cross: center)
{
  display: flex;
  flex-direction: $direction;
  justify-content: $main;
  align-items: $cross;
}
@mixin grid($columns, $rows)
{
  display: grid;
  grid-template-columns: repeat($columns, 1fr);
  grid-template-rows: repeat($rows, 1fr);
}
@mixin setColor($color, $backgroundColor)
{
  color: $color;
  background-color: $backgroundColor;
}
@mixin generic-button($backgroundColor, $color)
{
  padding: 0.3rem 0.6rem;
  border-radius: 0.3rem;
  @include setColor($color, $backgroundColor);
  height: auto;
  max-height: 100%;
}
@mixin generic-text-button($backgroundColor: rgb(85, 122, 226), $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  font-size: 1.2rem;
}
@mixin generic-icon-button($height: 100%, $backgroundColor: #333, $color: whitesmoke)
{
  @include generic-button($backgroundColor, $color);
  @include flex();
  i {font-size: 1.5rem;}
}

@mixin beat-container($width, $ratio, $columns)
{
  width: $width;
  aspect-ratio: $ratio;
  @include grid($columns, 2);
  gap: 2px;
  > *
  {
    @include setColor(whitesmoke, #333);
    border-radius: 5px;
    @include flex();
  }
  .selected {@include setColor(#333, whitesmoke);}
}
@mixin slider($chrome)
{
  -webkit-appearance: none;
  border: 1px solid #333;
  --width: 0.9rem;
  width: var(--width);
  aspect-ratio: 1 / 2;
  border-radius: 0.2rem;
  background: #AAA;
  cursor: pointer;
  @if $chrome
  {
    margin-top: calc(var(--width) * -0.8); /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  }
}
@mixin track()
{
  -webkit-appearance: none;
  background: #333;
  height: 0.3rem;
}