@import 'mixins';

.tutorial
{
  aspect-ratio: 1 / 1;
  max-width: 36rem;
  @media (min-aspect-ratio: 2/1){width: 90vh;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){width: 90vh;}
  @media (min-aspect-ratio: 1/2) and (max-aspect-ratio: 1/1)
  {
    transform: rotate(90deg);
    max-width: calc(90vw);
    min-width: calc(90vw);
  }
  @media (max-aspect-ratio: 1/2)
  {
    transform: rotate(90deg);
    width: 90vw;
  }
  // 0.3rem -> 5px
  position: absolute;
  background-color: whitesmoke;
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column, flex-start);
  gap: 0.6rem;
  overflow-y: auto;
  .section
  {
    width: 100%;
    border-radius: 0.6rem;
    padding: 0.6rem 1.2rem;
    background-color: rgb(174, 212, 213);
    font-size: 1rem;
    .title
    {
      font-size: 2rem;
      margin-bottom: 0.6rem;
    }
    img
    {
      max-width: 100%;
      margin: 0.6rem 0;
    }
  }
  button.close
  {
    @include generic-text-button();
    align-self: flex-end;
  }
}
.tutorial::-webkit-scrollbar {width: 10px;}
.tutorial::-webkit-scrollbar-track {background: whitesmoke;}
.tutorial::-webkit-scrollbar-thumb {background: #888;}
.tutorial::-webkit-scrollbar-thumb:hover {background: #555;}