@import 'mixins';

.beats
{
  aspect-ratio: 1 / 0.5;
  max-width: 72rem;
  @media (min-aspect-ratio: 2/1){height: 90vh;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){width: 90vw;}
  @media (min-aspect-ratio: 1/2) and (max-aspect-ratio: 1/1)
  {
    transform: rotate(90deg);
    max-width: calc(90vh);
    min-width: calc(90vh);
  }
  @media (max-aspect-ratio: 1/2)
  {
    transform: rotate(90deg);
    height: 90vw;
  }
  // 0.3rem -> 5px
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column, center, flex-start);
  --gap: 0.6rem;
  gap: var(--gap);
  > *{width: 100%;}
  background-color: darkgreen;
  .top
  {
    height: 20%;
    @include flex(row, flex-start);
    gap: 1.2rem;
    position: relative;
    .beat-letter-container{@include beat-container(9%, 1 / 1, 2);}
    .beat-number-container{@include beat-container(36%, 4 / 1, 8);}
    .buttons
    {
      position: absolute;
      right: 0;
      @include flex(column);
      gap: 0.6rem;
      > *
      {
        @include flex();
        gap: 0.6rem;
      }
      button
      {
        @include generic-text-button($backgroundColor: #333);
      }
      button.selected
      {
        background-color: orange;
        color: #333;
      }
    }
  }
  .main
  {
    height: calc(70% - 2 * var(--gap));
    @include flex(row, flex-start);
    gap: 0.6rem;
    > * {height: 100%;}
    .sounds-container
    {
      width: 20%;
      @include grid(1, 8);
      .sound
      {
        @include flex();
        @include setColor($color: whitesmoke, $backgroundColor: #333);
        border: 1px solid #AAA;
      }
    }
    .notes-container
    {
      aspect-ratio: 2 / 1;
      @include grid(16, 8);
      .note{border: 1px solid #AAA;}
      .note.selected{border: 1px solid #333;}
      .note.focus{border: 2px solid orange;}
      position: relative;
      .selection
      {
        border: 2px solid orange;
        position: absolute;
        background-color: rgba($color: #000000, $alpha: 0.2);
      }
      .track-line
      {
        position: absolute;
        height: 100%;
        width: 2px;
        background-color: orange;
      }
      .track-line.hidden
      {
        display: none;
      }
    }
    .note-options
    {
      @include flex(column, flex-start, flex-start);
      gap: 0.6rem;
      button { @include generic-text-button(); }
    }
    .note-options.hidden
    {
      display: none;
    }

  }
  .bottom
  {
    height: 10%;
    @include flex(row, space-between);
    > *
    {
      height: 100%;
      @include flex();
      gap: 0.6rem;
    }
    .left button { @include generic-icon-button($backgroundColor: whitesmoke, $color: #333); }
    .right button { @include generic-text-button(); }
  }
}