@import 'mixins';

.sounds
{
  aspect-ratio: 1 / 2;
  max-height: 36rem;
  @media (min-aspect-ratio: 2/1){height: 90vh;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){height: 90vh;}
  @media (min-aspect-ratio: 1/2) and (max-aspect-ratio: 1/1)
  {
    transform: rotate(90deg);
    max-height: calc(90vw);
    min-height: calc(90vw);
  }
  @media (max-aspect-ratio: 1/2)
  {
    transform: rotate(90deg);
    height: 90vw;
  }
  // 0.3rem -> 5px
  position: absolute;
  background-color: whitesmoke;
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  > * {width: 100%;}
  .top
  {
    height: 20%;
    @include flex(column);
    gap: 0.6rem;
    label {font-size: 1.2rem;}
    .input-container
    {
      width: 100%;
      @include flex();
      > *
      {
        height: 2rem;
        border: 1px solid #333;
      }
      input
      {
        width: 75%;
        padding: 0.3rem 0.6rem;
        font-size: 1rem;
      }
      input:focus-visible {outline: none;}
      button.search
      {
        @include setColor($color: whitesmoke, $backgroundColor: rgb(85, 122, 226));
        border-left: none;
        padding: 0.3rem 0.6rem;
        i{font-size: 1rem;}
      }
    }
  }
  .list
  { 
    height: calc(70% - 2 * var(--gap));
    @include flex(column, flex-start);
    gap: 0.6rem;
    overflow-y: scroll;
    overflow-x: hidden;
    .sound { @include generic-text-button($backgroundColor: #333); }
    .sound.selected
    {
      background-color: orange;
      color: #333;
    }
  }
  .list::-webkit-scrollbar {width: 10px;}
  .list::-webkit-scrollbar-track {background: whitesmoke;}
  .list::-webkit-scrollbar-thumb {background: #888;}
  .list::-webkit-scrollbar-thumb:hover {background: #555;}

  .bottom
  {
    @include flex();
    gap: 0.6rem;
    height: 10%;
    > * { @include generic-text-button(); }
  }
}