@import 'mixins';

.note-control
{
  aspect-ratio: 2 / 1;
  max-width: 30rem;
  @media (min-aspect-ratio: 2/1){width: 90vw;}
  @media (min-aspect-ratio: 1/1) and (max-aspect-ratio: 2/1){width: 90vw;}
  @media (min-aspect-ratio: 1/2) and (max-aspect-ratio: 1/1)
  {
    transform: rotate(90deg);
    max-width: calc(90vh);
    min-width: calc(90vh);
  }
  @media (max-aspect-ratio: 1/2)
  {
    transform: rotate(90deg);
    width: 90vh;
  }
  // 0.3rem -> 5px
  position: absolute;
  background-color: whitesmoke;
  border-radius: 0.6rem;
  padding: 0.6rem 1.2rem;
  @include flex(column);
  --gap: 0.6rem;
  gap: var(--gap);
  > * {width: 100%;}
  .input-container
  {
    height: calc(40% - var(--gap));
    @include flex(column);
    gap: 1.2rem;
    font-size: 1.2rem;
    > * 
    {
      width: 100%;
      @include flex();
      gap: 0.6rem;
    }
    input[type=range]{width: 80%;}
    input[type=range]::-webkit-slider-thumb {@include slider(true);}
    input[type=range]::-moz-range-thumb{@include slider(false);}
    input[type=range]::-ms-thumb{@include slider(false);}
    input[type=range]::-webkit-slider-runnable-track{@include track();}
    input[type=range]::-moz-range-track{@include track();}
    input[type=range]::-ms-track{@include track();}
  }
  .buttons
  {
    @include flex(row, flex-end);
    button.close {@include generic-text-button();}
  }
}